	module.exports = {"data":{"markdownRemark":{"html":"<p>Yes, it is. </p>\n<p>Open source software is free. It is supported by communities of developers, not corporations. And it is ready.</p>\n<p>Sure, sometimes, it doesn't work. Development can stop abruptly on a project, and support can be limited — because it's free. </p>\n<p>But often, open source software is better than the overloaded, buggy programs put out by the big guys (Intel, Microsoft, Cisco, etc.).  </p>\n<p>With a little bit of online research, there is often an open-source alternative to expensive programs that are needed for programs in rural/poor/developing areas.</p>\n<p>What open source gives you (besides a free or cheap program) is a connection with software develpers that are, typically, committed to a <a href=\"http://en.wikipedia.org/wiki/Open_source\">cause</a> that appears to be very much in line with the general philosophies underlying ICT and development work. </p>\n<p>Power to the people. </p>\n<p>Here's a bit from recent BBC article describing the use of open source software in Manchester schools, and the government research that supports its use. (Hard not to like thousands of Pounds in savings, right?)</p>\n<p>I say, if it's good enough for Manchester, it's good enough for the world. Now if only there were open source programs comparable to Intel's Teach to the Future Program, which claims to have trained 2 million teachers. Too bad they're all trained in super-expensive proprietary software. </p>\n<blockquote class=\"large\">\n<p>Software licenses cost Parrs Wood [elementary school] about 30,000 pounds each year, less than half the cost if no OSS [open source software] were deployed, according to figures in the recent Becta [UK education research group] report.</p>\n<p>Only recently has the school become satisfied that OSS is now sufficiently well developed to meet classroom and office needs and provides a viable alternative to licensed software.</p>\n<p>With governors' support and encouragement, the school is adopting OSS more completely over the next three years, including the eventual replacement of Windows by an OSS desktop, which will be a significant change.</p>\n<p>Read the entire article:\n<a title=\"BBC NEWS | Education | How schools can get free software\" href=\"http://news.bbc.co.uk/1/hi/education/4642461.stm\">BBC NEWS | Education | How schools can get free software</a></p>\n</blockquote>","frontmatter":{"created_at":"December 21, 2004","path":"/2004/12/21/is-open-source-software-ready","title":"Is Open Source Software Ready?","tags":["opensource","education","community"]}}},"pathContext":{}}

